footer{
    background: var(--darkerPink);        
    text-align: left;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100vw;

    &>.leftFDiv{  
        @media (min-width: 768px) {
            width:fit-content;
        }
        @media (max-width: 768px) {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        &>.fuDiv{
            font-family: "FZ Xiaozhuan";
            color: var(--lightPink);
            position: relative;
            width:fit-content;
            height: fit-content;
            &>#fu{
                font-size: 100px;
                opacity: .5;
                transition: .3s;
                cursor: pointer;

                &:hover{
                    opacity: 1;
                }
            }
        }
        &>.socialFDiv>a{
            display: flex;
            flex-direction: column;
            color: var(--lightPink);
            font-size: 15px;
    
            &:hover{
                text-decoration: underline;
            }
        }

        &>.socialFDiv>span{
            display: flex;
            flex-direction: column;
            color: var(--lightPink);
            font-size: 15px;
            font-weight: 500;
        }
    }

    &>.textFDiv{
        margin-right: 1em;
        &>.footerSpan{
            font-size: 15px;
            color: var(--lightPink);
            text-shadow: 5px 5px 5px var(--darkPinkShadow);
            line-height: 1em;
            @media (max-width: 768px) {
                font-size: 15px;
            }
            font-style: italic;
        }
    }

}

.footerIcon{
    width: 15px;
    display: inline;
    line-height: 10px;
}

.footerDiv{
    z-index: 50;
}

